.proficiencyWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 97px;
}

.proficiency {
    width: 49%;
}

.header {
    font-size: 50px !important;
    font-weight: bold;
    margin-bottom: 10px;
}

.proficiency p {
    font-size: 23px;
    margin-top: 13px;
}

.proficiency progress {
    height: 28px;
    width: 100%;
}

.myImage {
    width: 45%;
    height: 560px;
}

@media screen and (max-width: 1457px) {
    .proficiency {
        width: 100%;
    }

    .myImage {
        display: none;
    }
}

@media screen and (max-width: 770px) {
    .header {
        text-align: center;
    }
}