.bigProjectsWrapper {
    margin: 120px 0px 20px 0px;
}

.bigProjects {
    font-size: 50px;
    font-weight: bold;
}

.description {
    color: #868E96;
    margin: 19px 0px;
    font-size: 19px;
}

.cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 1rem 1rem;
    gap: 1rem 1rem;
}