.programmingLanguage {
    max-width: 80px;
    margin: 0px 20px 20px 0px;
    text-align: center;
}

.programmingLanguage img {
    width: 100%;
    height: 80px;
}

.languageName {
    margin-top: 7px;
    font-size: 18px;
    font-family: 'Roboto', sans-serif;
}

@media screen and (max-width: 520px) {
    .programmingLanguage {
        width: 90px;
        margin-right: 17px;
    }
}