.projects {
    padding-top: 58px;
}

.projectsHeader {
    font-size: 50px;
    font-weight: bold;
    margin-bottom: 30px;
}

.cards {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.moreProjects {
    display: block;
    background: red;
    padding: 15px;
    color: white;
    border: none;
    border-radius: 5px;
    margin: 20px auto 0;
    font-weight: 600;
    font-size: 17px;
}

.more_projects:hover {
    background: white;
    border: 1px solid red;
    color: red;
    transition: 0.5s;
}

@media screen and (max-width: 770px) {
    .projects {
        margin-top: 75px;
    }

    .projectsHeader {
        text-align: center;
        font-size: 32px;
        margin-bottom: 20px;
    }

    .more_projects {
        margin-top: 39.9px;
    }
}