.card {
    padding: 24px;
    text-align: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card:hover {
    box-shadow: 0 10px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    transition: 0.5s;
}

.card img {
    height: 250px;
    width: 250px;
    margin-bottom: 15px;
}

.project_title {
    font-weight: 700;
    font-size: 21px;
}

.projectDescription {
    margin: 17px 0px;
    font-size: 17px;
    color: #868E96;
}

.visitButton {
    background: red;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    font-size: 12px;
    padding: 7px;
}

@media only screen and (max-width: 685px) {
    .card:first-child {
        margin-right: 0;
    }
}