.experiences {
    margin-top: 170px;
}

.header_experiences {
    font-size: 50px;
    font-weight: bold;
}

.cardsBlock {
    display: flex;
    justify-content: space-between;
    margin-top: 3%;
}

.experiences_card {
    width: 32.696759%;
    background: #FFFFFF;
    text-align: center;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 10px;
}

.experiences_card:last-child {
    margin-right: 0;
}

.experiences_card:hover {
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.company {
    position: relative;
    padding: 72px 0px;
    background-color: #4B4B4B;
    color: white;
    font-size: 27px;
    border-radius: 5px;
}

.aboutExperience {
    padding: 1.5rem;
}

.company_img {
    position: absolute;
    right: 50%;
    top: 27%;
    transform: translate(50%, 50%);
    height: 128px;
    box-shadow: 0 0.5rem 1rem rgb(0 0 0 / 30%);
    border-radius: 100px;
}

.aboutExperience {
    margin-top: 56px;
}

.job {
    font-size: 22px;
    font-weight: 700;
}

.date {
    margin: 16px 0px 19px 0px;
    font-size: 18px;
    font-weight: 700;
}

.impressions {
    color: #868E96;
    font-size: 19px;
}

@media only screen and (max-width: 1380px) {
    .company_img {
        height: 104px;
        top: 35.9%;
    }

    .aboutExperience {
        margin-top: 48px;
    }
}

@media only screen and (max-width: 1000px) {
    .cardsBlock {
        flex-wrap: wrap;
    }

    .experiences_card {
        flex: unset;
        width: 48.88%;
        margin-bottom: 17px;
    }

    .experiences_card:nth-child(even) {
        margin-right: 0;
    }
}

@media only screen and (max-width: 769px) {
    .impressions {
        font-size: 16px;
    }
}

@media only screen and (max-width: 660px) {
    .experiences_card {
        width: 100%;
    }
}