@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

.greetingWrapper {
    display: flex;
    align-items: center;
    margin-top: 98.9px;
    justify-content: space-between;
}

.left_block {
    width: 49%;
}

.greeting {
    font-weight: bold;
    font-size: 70px;
}

.aboutMe {
    font-size: 33.9px;
    color: #868E96;
    margin-top: 50px;
}

.buttons {
    margin: 50px 0px 0px 0px;
}

button {
    color: white;
    background: red;
    border: none;
    border-radius: 5px;
    padding: 13px 22px;
    font-size: 16px;
}

.resume {
    margin-left: 50px;
}

button:hover {
    font-size: 17px;
}

.myPhoto {
    width: 49%;
}

@media screen and (max-width: 1380px) {
    .greeting {
        font-size: 40px;
    }

    .aboutMe {
        margin-top: 35px;
        font-size: 21px;
    }
}

@media screen and (max-width: 900px) {
    .greetingWrapper {
        flex-direction: column;
        margin: 87px auto 0;
    }

    .left_block {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-width: unset;
        width: unset;
        text-align: center;
    }

    .aboutMe {
        margin-top: 21px;
    }

    .buttons {
        display: flex;
        width: 100%;
        justify-content: space-around;
    }

    button {
        font-size: 15px;
    }

    .resume {
        margin-left: 0;
    }

    .myPhoto {
        width: 312px;
        height: 256px;
        margin: 30px 0px 0px 0px;
    }
}

@media screen and (max-width: 365px) {
    button {
        padding: 12px 18px;
        font-size: 12px;
    }
}